import { Col, Row } from "react-bootstrap"
import Container from "react-bootstrap/Container"

const Contact_box=()=>{
    return(
        <div style={{backgroundImage: 'url("./bg.png")', paddingTop: '5rem', paddingBottom: '5rem', backgroundSize: 'cover', height: '100%', backgroundPosition: 'center' }}>
            <Container>
                <Row>
                    <Col sm={6} style={{textAlign:'center',margin:'auto' ,color:'white'}}>
                        <div>
                        <h2><strong>Quick ID</strong></h2>
                        <br/>
                        <h3>Phone: 0985-922-487</h3>
                        <h3>Email: doncechenjun@gmail.com</h3>
                        </div>
                    </Col>
                    <Col sm={6}>
                        <iframe 
                        width="100%" 
                        height="600px" 
                        src="https://www.surveycake.com/s/OKBv7" 
                        allowTransparency="true" 
                        frameborder="0">
                        </iframe>
                    </Col>
                </Row>
                
            </Container>
  
        </div>
    )
}

export default Contact_box