import { Col, Row } from "react-bootstrap"
import Container from "react-bootstrap/Container"

function mouseover(id_to_shown){
    let ele=document.getElementById(id_to_shown);
    ele.style.visibility = 'visible';
    
}
function mouseleave(id_to_shown){
    let ele=document.getElementById(id_to_shown);
    ele.style.visibility = 'hidden';
}

const Portfolio_box=()=>{
    let col_Style={
        border:'0',
        padding:'0'
    }
    
    let div_Style={
        margin:'5px',
        border:'black 2px solid',
        overflow:'hidden',
        position:'relative',
        textAlign:'center'
    }

    let img_Style={
        position: 'relative',
        left:'50%',
        transform:'translate(-50%)',
        height:'40vh',
        objectFit:'cover',
        
    }

    let p_style={
        position:'absolute',
        top:'0',
        left:'0',
        height:'40vh',
        width:'100%',
        backgroundColor:'rgba(60,60,60,0.7)',
        visibility : 'hidden',
        color:'white'
    }

    
    return(
        <Container style={{width:'50%'}}>
            <Row >
                <Col sm={8} style={col_Style}>
                    <div style={div_Style} 
                    onMouseOver={()=>{mouseover("mdserver_txt")}}
                    onMouseLeave={()=>{mouseleave("mdserver_txt")}}>
                        <img src='./portfolio_tb/mdserver.png' style={img_Style}/>
                        <div id="mdserver_txt" style={p_style}>
                            <p style={{position:'relative',top:'40%'}}>A markdown file note server</p>
                        </div>
                    </div>
                </Col>
                <Col sm={4} style={col_Style}>
                <div style={div_Style} 
                    onMouseOver={()=>{mouseover("mindmap_txt")}}
                    onMouseLeave={()=>{mouseleave("mindmap_txt")}}>
                        <img src='./portfolio_tb/mindmap.png' style={img_Style}/>
                        <div id="mindmap_txt" style={p_style}>
                            <p style={{position:'relative',top:'40%'}}>A mindmap generator</p>
                        </div>
                    </div>
                    
                </Col>
            </Row>
            <Row>
                <Col sm={5} style={col_Style}>
                    <div style={div_Style} 
                        onMouseOver={()=>{mouseover("crystall_txt")}}
                        onMouseLeave={()=>{mouseleave("crystall_txt")}}>
                        <img src='./portfolio_tb/crystall.png' style={img_Style}/>
                        <div id="crystall_txt" style={p_style}>
                            <p style={{position:'relative',top:'40%'}}>Using html canvas element to simulate crystall structor</p>
                        </div>
                    </div>
                </Col>
                <Col sm={7} style={col_Style}>
                    <div style={div_Style} 
                            onMouseOver={()=>{mouseover("landingpage_txt")}}
                            onMouseLeave={()=>{mouseleave("landingpage_txt")}}>
                        <img src='./portfolio_tb/landingpage.png' style={img_Style}/>
                        <div id="landingpage_txt" style={p_style}>
                            <p style={{position:'relative',top:'40%'}}>A landing page for a online service</p>
                        </div>
                    </div>
                </Col>
            </Row>
    </Container>
    )
}

export default Portfolio_box